//Vari√°veis
$bg_dark: #151718;
$white: #fff;
$white_op: rgba(255, 255, 255, 0.1);
$purple: #9174E4;

//Fontes
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
    padding: 0;
    margin: 0;
    border: 0;
    font-family: Poppins;
}

body {
    background-color: #151718;
}

.container {
    max-width: 1440px;
    height: 95vh;
    margin: auto;
}

.container-box {
    max-width: 1100px;
    margin: auto;
}

.nav-container {
    margin-top: 48px;
    width: 100%;
    height: 40px;
    position: relative;
    color: $white;
    font-size: 18px;
}

.nav-left {
    position: absolute;
    left: 0;
    top: 10px;
    z-index: 1;
    cursor: pointer;
}

.nav-center {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;

    a {
        text-decoration: none;
    }

    ul {
        line-height: 40px;
        height: 40px;
        width: 120px;
        text-align: center;
        cursor: pointer;
        justify-content: space-between;
        color: $white;
    }

    ul:hover {
        transform-origin: 50% 50% 0px;
        transition: ease-in-out 0.2s;
        content: '';
        background-color: $white_op;
        border-radius: 8px;
    }
}

.nav-right {
    position: absolute;
    right: 0;
    top: 8px;

    img {
        cursor: pointer;
    }
}

.container-box {
    position: relative;
    height: 91.3vh;
    display: flex;
    flex-direction: row;
    align-items: center;

    h1 {
        color: $white;
        font-size: 48px;
        font-weight: 600;
    }

    span {
        color: $purple;
    }

    a, h2 {
        font-weight: 400;
        color: $white;
        font-size: 16px;
    }

    img {
        position: absolute;
        width: 550px;
        height: 425px;
        right: 0;
        image-rendering: optimizeQuality;
    }
}